import { Pagination, Button, Select, message } from "antd";
import Loader from "../../../common/loader/Loader";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getPayInReport } from "../../../api/login/Login";
import { FaSort } from "react-icons/fa";

function PayInReport() {
  const [data, setData] = useState([]);
  const [excelData, setExcelData] = useState([]);
  const [loading, setLoading] = useState(false);

  const [page, setPage] = useState(0);
  const [count, setCount] = useState(10);
  const [totalCount, setTotalCount] = useState(0);

  const [filter, setFilter] = useState({
    start_date: "",
    end_date: "",
    status: "",
    order_id: "",
    txn_id: "",
  });

  const currentDate = new Date().toISOString().split("T")[0];
  const safe = (val, fallback = "-") =>
    val === undefined || val === null || val === "" ? fallback : val;

  const fetchReport = async (overrideFilter) => {
    try {
      setLoading(true);

      const activeFilter = overrideFilter || filter;
      if (activeFilter.start_date && activeFilter.end_date) {
        if (
          new Date(activeFilter.start_date) > new Date(activeFilter.end_date)
        ) {
          message.error("Wrong Date Combination");
          return; // STOP API CALL
        }
      }

      const query = new URLSearchParams({
        pageNumber: page + 1,
        pageSize: count,
        start_date: activeFilter.start_date || "",
        end_date: activeFilter.end_date || "",
        status: activeFilter.status === "ALL" ? "" : activeFilter.status || "",
        merchantOrderId: activeFilter.order_id || "",
        txnId: activeFilter.txn_id || "",
      }).toString();

      const res = await getPayInReport(query);

      setData(res?.data?.data);
      setExcelData(res?.data?.data);
      setTotalCount(res?.data?.pagination?.totalRecords);
    } catch (error) {
      console.log(error);
      message.error(error?.response?.data?.message || "Something went wrong!");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchReport();
  }, [page, count]);

  const handlePageSizeChange = (value) => {
    setCount(value);
    setPage(0);
  };

  const onChangeVal = (value) => {
    setPage(value - 1);
  };
  const handleChange = (e) => {
    const { name, value } = e.target;

    setFilter((prev) => {
      if (name === "start_date" && prev.end_date && value > prev.end_date) {
        return {
          ...prev,
          start_date: value,
          end_date: value,
        };
      }

      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const ResetData = () => {
    const empty = {
      start_date: "",
      end_date: "",
      status: "",
      order_id: "",
      txn_id: "",
    };

    setFilter(empty);
    setPage(0);
    fetchReport(empty); // <-- use empty filters immediately
  };

  const handleSearch = () => {
    if (filter.start_date && filter.end_date) {
      if (new Date(filter.start_date) > new Date(filter.end_date)) {
        message.error("Wrong Date Combination");
        return;
      }
    }
    setPage(0);
    fetchReport(filter);
  };

  return (
    <>
      {loading && <Loader />}
      <div className="PageHeading">
        <h1
          style={{
            color: "var(--bs-primary2)",
            fontWeight: 600,
          }}
        >
          Online Payment Transaction Report
        </h1>
      </div>

      <div
        className="ContentArea"
        style={{
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          border: "1px solid var(--bs-primary2)",
          borderRadius: "12px",
          padding: "16px",
        }}
      >
        <div
          className="card"
          style={{
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
            borderRadius: "12px",
          }}
        >
          <div
            className="card-header"
            style={{
              backgroundColor: "var(--bs-primary3)",
              color: "var(--bs-primary2)",
              fontWeight: 600,
            }}
          ></div>
          <div className="card-body">
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-3">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control input-white-bg"
                  max={currentDate}
                  value={filter.start_date}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control input-white-bg"
                  max={currentDate}
                  value={filter.end_date}
                  onChange={handleChange}
                  min={filter.start_date || undefined}
                  onBlur={() => {
                    if (
                      filter.start_date &&
                      filter.end_date &&
                      filter.end_date < filter.start_date
                    ) {
                      message.error(
                        "End date cannot be earlier than start date",
                      );
                      setFilter((prev) => ({
                        ...prev,
                        end_date: prev.start_date,
                      }));
                    }
                  }}
                />
              </div>

              <div className="form-group col-md-3">
                <label>Status</label>
                <select
                  className="form-select"
                  name="status"
                  value={filter.status}
                  onChange={handleChange}
                >
                  <option value="" disabled>
                    Select Status
                  </option>
                  <option value="ALL">All</option>
                  <option value="PENDING">Pending</option>
                  <option value="SUCCESS">Success</option>
                  <option value="FAILED">Failed</option>
                  <option value="REJECT">Rejected</option>
                </select>
              </div>

              <div className="form-group col-md-3">
                <label>Order Id</label>
                <input
                  type="text"
                  name="order_id"
                  className="form-control bs-autofill-fix"
                  placeholder="Enter Order Id"
                  value={filter.order_id}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-3">
                <label>Txn Id</label>
                <input
                  type="text"
                  name="txn_id"
                  className="form-control bs-autofill-fix"
                  placeholder="Enter Txn Id"
                  value={filter.txn_id}
                  onChange={handleChange}
                />
              </div>

              <div className="form-group col-md-12">
                <label>&nbsp;</label>

                <button
                  type="button"
                  className="btn"
                  onClick={handleSearch}
                  style={{
                    backgroundColor: "#6C757D",
                    color: "#fff",
                    border: "1px solid var(--bs-primary2)",
                    fontWeight: 600,
                    padding: "6px 16px",
                    borderRadius: "10px",
                    marginRight: "8px",
                  }}
                >
                  Search
                </button>

                <button
                  type="button"
                  className="btn"
                  onClick={ResetData}
                  style={{
                    backgroundColor: "var(--bs-primary1)",
                    color: "var(--bs-primary2)",
                    border: "1px solid var(--bs-primary2)",
                    fontWeight: 600,
                    padding: "6px 16px",
                    borderRadius: "10px",
                  }}
                >
                  Reset
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Table */}
        <div
          className="card mt-4"
          style={{
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
            borderRadius: "12px",
          }}
        >
          <div className="card-body">
            <div className="d-flex justify-content-end align-items-center mb-3">
              <Button
                onClick={() => document.getElementById("excel-btn").click()}
                style={{
                  backgroundColor: "#E8DAD6",
                  color: "var(--bs-primary2)",
                  border: "1px solid var(--bs-primary2)",
                }}
              >
                Download Excel
              </Button>
              <ReactHTMLTableToExcel
                id="excel-btn"
                className="d-none"
                table="table-to-xlsx"
                filename="PayIn_Report"
                sheet="Sheet1"
                buttonText="Download Excel"
              />
            </div>

            <div style={{ overflow: "auto" }}>
              <table
                className="table table-striped table-bordered table-hover"
                style={{
                  backgroundColor: "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                }}
              >
                <thead
                  style={{
                    backgroundColor: "var(--bs-primary3)",
                    color: "var(--bs-primary2)",
                    fontWeight: 600,
                  }}
                >
                  <tr>
                    <th className="text-center er-th">
                      S.No <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Txn Date <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Order ID <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Txn ID <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Amount <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Charges <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Final Amount <FaSort />
                    </th>
                    <th className="text-center er-th">
                      Status <FaSort />
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {data?.length > 0 &&
                    data.map((item, i) => (
                      <tr key={item._id}>
                        <td>{i + 1 + page * count}</td>
                        <td>{new Date(item?.createdAt).toLocaleString()}</td>
                        <td>{item?.merchantOrderId}</td>
                        <td>{item?.txnId}</td>
                        <td>{item?.amount}</td>
                        <td>{item?.charges}</td>
                        <td>{item?.final_amount}</td>
                        <td style={{ whiteSpace: "nowrap" }}>
                          <span
                            style={{
                              display: "inline-block",
                              padding: "6px 14px",
                              borderRadius: "999px",
                              fontSize: "12px",
                              fontWeight: 600,
                              color: "#fff",
                              textTransform: "capitalize",
                              backgroundColor:
                                item?.status === "SUCCESS"
                                  ? "#16a34a" // green
                                  : item?.status === "PENDING"
                                    ? "#f59e0b" // orange
                                    : item?.status === "PROCESSING"
                                      ? "#0ea5e9" // blue
                                      : item?.status === "REJECT"
                                        ? "#dc2626" // red
                                        : "#6b7280", // gray
                            }}
                          >
                            {safe(item?.status)}
                          </span>
                        </td>
                      </tr>
                    ))}

                  {data?.length === 0 && (
                    <tr>
                      <td colSpan="10" style={{ textAlign: "center" }}>
                        No Record Found
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>

          {/* Pagination & count */}
          <div className="dataTables_info_page d-flex justify-content-between align-items-center p-3">
            <div className="d-flex align-items-center">
              <label className="me-2">Show</label>
              <Select
                value={count}
                onChange={handlePageSizeChange}
                style={{ width: 120 }}
                options={[
                  { value: 10, label: "10 / page" },
                  { value: 20, label: "20 / page" },
                  { value: 50, label: "50 / page" },
                  { value: 100, label: "100 / page" },
                ]}
              />
              <div className="ms-3">Total {totalCount} entries</div>
            </div>

            <Pagination
              pageSize={count}
              current={page + 1}
              total={totalCount}
              onChange={onChangeVal}
              showSizeChanger={false}
              style={{ color: "var(--bs-primary2)" }}
            />
          </div>
        </div>
      </div>

      {/* Excel Table */}
      <table className="d-none" id="table-to-xlsx">
        <thead>
          <tr>
            <th>Created Date</th>
            <th>Txn Id</th>
            <th>Order Id</th>
            <th>Amount</th>
            <th>Charges</th>
            <th>Final Amount</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          {excelData?.map((item) => (
            <tr key={item._id}>
              <td>{item?.createdAt}</td>
              <td>{item?.txnId}</td>
              <td>{item?.merchantOrderId}</td>
              <td>{item?.amount}</td>
              <td>{item?.charges}</td>
              <td>{item?.final_amount}</td>
              <td>{item?.status}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
}

export default PayInReport;
